.tile {
    display: flex;
    align-items: center;
    background: #6660607a;
    /* justify-content: space-between; */
    width: 80%;
    padding: 10px;
    border-radius: 10px;
    height: fit-content;
    line-height: 15px;
    margin-bottom: 10px;
}

.tile-light {
    display: flex;
    align-items: center;
    background: #ffffff;
    /* justify-content: space-between; */
    width: 80%;
    padding: 10px;
    border-radius: 10px;
    height: fit-content;
    line-height: 15px;
    margin-bottom: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
}

.tile2 {
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
    width: 100%;
    /* padding: 10px; */
    border-radius: 10px;
    height: fit-content;
    line-height: 15px;
}

.tile:hover {
    background: #666060ef;
    cursor: pointer;
}

.avatar {
    width: 55px;
    height: 55px;
    border-radius: 50%;
    background: #282c34;
    margin-right: 10px;
    padding: 10px;
}

.title {
    font-size: calc(6px + 2vmin);
}

.username {
    font-size: calc(2px + 2vmin);
}

.sun {
    background: linear-gradient(white, gold);
}

.moon {
    background: linear-gradient(rgb(61, 56, 56), rgb(0, 0, 0));
}

.leaf {
    background-image: url('/src/svgs/cdnlogo.com_mongodb-icon.svg');
    background-color: transparent;
    background-repeat: no-repeat;
    width: 60px;
}

.conn {
    background-image: url('/src/svgs/lock.svg');
    background-color: transparent;
    background-repeat: no-repeat;
}

.wipe {
    background-image: url('/src/svgs/watchman-monitoring.svg');
    background-color: transparent;
    background-repeat: no-repeat;
}

.edit {
    float: right;
}

.edit-icon {
    width: 29px;
    height: 29px;
    background-image: url('/src/svgs/pen.svg');
    background-repeat: no-repeat;
    margin-right: 13px;
}

.edit-icon:hover {
    background-image: url('/src/svgs/pen2.svg');
}

.edit-icon-light {
    background-repeat: no-repeat;
    margin-right: 13px;
    width: 29px;
    height: 29px;
    background-image: url('/src/svgs/light-pen.svg');
}


