.section-view {
    background: #6660607a;
    list-style-type: none;
    margin: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
    padding: 10px;
    font-size: calc(5px + 2vmin);
    width: 100%;
    height: 51vmin;
    flex-direction: column;
}

.section-view-light {
    background: #ffffff;
    list-style-type: none;
    margin: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
    padding: 10px;
    font-size: calc(5px + 2vmin);
    width: 100%;
    height: 51vmin;
    flex-direction: column;
}

.section-func {
    background: #6660607a;
    list-style-type: none;
    margin: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    font-size: calc(5px + 2vmin);
    width: 100%;
    height: 5vmin;
}

.section-func-light {
    background: #ffffff;
    list-style-type: none;
    margin: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    font-size: calc(5px + 2vmin);
    width: 100%;
    height: 5vmin;
}

.section-func ul li {
    display: inline-block;
    padding: 10px;
    padding-left: 30px;
}

.section-func-light ul li {
    display: inline-block;
    padding: 10px;
    padding-left: 30px;
}

.top-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    font-size: calc(5px + 2vmin);
}

.log-out {
    font-size: calc(1px + 2vmin);
    text-align: left;
    width: 100%;
    font-family: 'Courier New', monospace;
    overflow-y: auto;
}

.log-out::-webkit-scrollbar {
    width: 1em;
}

.log-out::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.log-out::-webkit-scrollbar-thumb {
    background-color: darkgrey;
    outline: 1px solid slategrey;
}

.ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
}