.headerWrap {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: calc(20px + 2vmin);
    justify-content: space-between;
    margin: 15px;
    width: 95%;
    height: 100px;
    /* border: 1px solid #ccc;
    border-radius: 12px; */
}

.header {
    display: flex;
    flex-direction: row;
}

.centered {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
}

.tabs {
    font-size: calc(10px + 2vmin);
    list-style-type: none;
}

.tabs li {
    display: inline-block;
    padding: 10px;
}

.input-search {
    background-color: #282c34;
    border: 1px solid #ccc;
    color: #ccc;
    border-radius: 5px;
    height: 20px;
}

.btn {
    height: 24px;
    background-color: #282c34;
    border-radius: 5px;
    border: 1px solid #ccc;
    color: white;
}

.btn-light {
    height: 24px;
    background-color: #ffffff;
    border-radius: 5px;
    border: 1px solid #ccc;
}