.ul-nav li {
    background: #6660607a;
    list-style-type: none;
    margin: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    font-size: calc(5px + 2vmin);
}

.ul-nav li:hover {
    background: #666060ef;
    cursor: pointer;
    ;
}

.nav-light li {
    background: #ffffff;
    list-style-type: none;
    margin: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    font-size: calc(5px + 2vmin);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
}

.nav-light li:hover {
    background: #6660607a;
    cursor: pointer;
    ;
}

.nav-wrap {
    width: 30%;
}

.badge {
    border-radius: 50%;
    background-color: #282c34;
    padding-left: 4px;
    padding-right: 4px;
}

.badge-light {
    background: #6660607a;
    border-radius: 50%;
    padding-left: 4px;
    padding-right: 4px;
}