.user-image {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #6660607a;
    width: 150px;
    height: 150px;
    border-radius: 50%;
}

.user-wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    width: 30%;
    height: 55vmin;
}

.user -name {
    text-align: center;
}

.details {
    font-size: calc(1px + 2vmin);
    font-family: 'Courier New', monospace;
    list-style-type: none;
    /* margin-top: 120px; */
}

.details li {
    padding: 10px;
}