.form_wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 500px;
}

.form_wrap-light {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 500px;
}

.form_wrap input {
    margin: 10px;
    width: 30%;
    padding: 10px;
    border: none;
    background: #6660607a;
    border-radius: 5px;
    color: white;
    /* outline: 2px solid burlywood; */
}

.form_wrap-light input {
    margin: 10px;
    width: 30%;
    padding: 10px;
    border: none;
    background: #ffffff;
    border-radius: 5px;
    color: #62646a;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
    /* outline: 2px solid burlywood; */
}

.form_wrap button {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
    width: 31.5%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    background: #6660607a;
    padding-bottom: 10.5px;
    padding-top: 10.5px;
    cursor: pointer;
    color: white;
}

.form_wrap-light button {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
    width: 31.5%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    background: #f5f5f5;
    padding-bottom: 10.5px;
    padding-top: 10.5px;
    cursor: pointer;
    color: #62646a;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
}

.input::-webkit-input-placeholder {
    color: white;
}

.input-light::-webkit-input-placeholder {
    color: #62646a;
}

.loader {
    width: 2vmin;
    height: 2vmin;
    border-radius: 50%;
    border: 2px solid white;
    border-bottom: 2px solid#6660607a;
    animation: loader-spin infinite 2s linear;
}

@keyframes loader-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

small {
    font-size: 10px;
}

a {
    color: orange;
    text-decoration: none;
}